// 导入React的钩子函数以及Realm的钩子。
import { useCallback, useEffect, useState } from 'react';
import { useQuery, useRealm, useUser } from '@realm/react';

// 导入Task模型。
import { StickerData, StickerJSON } from '../models/Task';
import { v4 as uuidv4 } from 'uuid';
import { json } from 'stream/consumers';
import { Results } from 'realm';

/**
 * useTaskManager是一个自定义钩子，提供对Realm数据库中任务的管理功能，
 * 包括添加、更新和删除任务。
 */
export function useTaskManager() {
  const realm = useRealm(); // 获取当前Realm的实例。
  const user = useUser(); // 获取当前登录的用户。
  const [requeryFlag, setRequeryFlag] = useState(false); // 用于触发重新查询的临时标志。

  // 使用useQuery钩子订阅Task模型的数据，useQuery返回一个实时更新的任务集合。
  let stickers = useQuery(StickerData, (collection) => collection, [requeryFlag]).filter(function(currentElement) {
    let data = JSON.parse(currentElement.data)
    return data.userID == user.id
  });
  // const stickers = useQuery(StickerData, (collection) => collection, [requeryFlag]).filter(function(currentElement) {
  //   let data = JSON.parse(currentElement.data)
  //   return data.userID == user.id
  // });;

  useEffect(() => {
    // 当组件加载时，更改requeryFlag的状态来确保useQuery可以获得最新的数据。
    // 这是一个暂时的解决方案，用于使useQuery更新tasks引用。
    setRequeryFlag(true);
  }, []);

  // 添加任务到数据库的函数。
  const addSticker = useCallback((description: string) => {
    realm.write(() => {
      // 创建一个新的Task对象，并设置_id和uuid属性。
      let newUUID: string = uuidv4()
      let newDefaultData = new StickerJSON()
      newDefaultData.uuid = newUUID
      newDefaultData.content = ""
      newDefaultData.userID = user.id
      let josnStr = JSON.stringify(newDefaultData)
      realm.create(StickerData, {
        _id: newUUID,
        uuid: newUUID,
        data: josnStr,
      });
    });
  }, [realm, user.id]); // 依赖于realm实例和当前用户的ID。

  // 切换任务完成状态的函数。
  // const toggleTaskStatus = useCallback((task: Task) => {
  //   realm.write(() => {
  //     // 直接在Task对象上修改isComplete属性来更新任务状态。
  //     task.isComplete = !task.isComplete;
  //   });
  // }, [realm]); // 依赖于realm实例。

  const updateStickerContent = useCallback((sticker: StickerData, content: string) => {
    realm.write(() => {
      //更新Sticker 内容
      //取出Data
      let oldStickerData = JSON.parse(sticker.data)
      //更新Data
      oldStickerData.content = content
      //放回Data
      let newStickerDataStr = JSON.stringify(oldStickerData)
      sticker.data = newStickerDataStr
    });
  }, [realm]); // 依赖于realm实例。

  // 删除数据库中的任务的函数。
  const deleteSticker = useCallback((sticker: StickerData) => {
    realm.write(() => {
      // 删除传入的Task对象。
      realm.delete(sticker);
    });
  }, [realm]); // 依赖于realm实例。

  // 返回任务数据和管理任务的函数。
  return {
    stickers,
    addSticker,
    updateStickerContent,
    deleteSticker,
  };
}
