// 从react库中导入所需的Hooks函数
import { FormEvent, useEffect, useState } from 'react';
// 导入react-router-dom库中的Navigate组件，用于路由跳转
import { Navigate } from 'react-router-dom';
// 导入@realm/react库中相关的功能，用于处理认证
import { AuthOperationName, useApp, useEmailPasswordAuth } from '@realm/react';

// 导入登录页的logo图片
import logo from '../assets/atlas-app-services.png';
// 导入登录页样式模块
import styles from '../styles/LoginPage.module.css';

/**
 * 登录和注册App服务应用的页面组件
 */
export function LoginPage() {
  // 使用useApp Hook获取应用实例
  const atlasApp = useApp();
  // 使用useEmailPasswordAuth Hook来处理注册和登录操作，并获取结果
  const { register, logIn, result } = useEmailPasswordAuth();
  // 使用useState Hook管理email和password的状态
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  // 使用useState Hook来管理用户是想登录还是注册
  const [authRequest, setAuthRequest] = useState<'login' | 'register'>('login');

  // 使用useEffect Hook，在注册成功后自动登录用户
  useEffect(() => {
    if (result.operation === AuthOperationName.Register && result.success) {
      logIn({ email, password });
    }
  }, [result.operation, result.success, logIn, email, password]);

  // 如果当前用户已经登录成功，则使用<Navigate>组件重定向到任务页面
  if (atlasApp.currentUser) {
    return <Navigate to='/tasks' />;
  }

  // 处理表单提交的函数
  const handleSubmit = (event: FormEvent<HTMLFormElement>): void => {
    // 阻止表单的默认提交行为
    event.preventDefault();

    // 根据authRequest的值来决定是登录还是注册
    if (authRequest === 'register') {
      register({ email, password });
    } else {
      logIn({ email, password });
    }
  };

  // 处理登录和注册按钮点击的函数
  const handleButtonClicked = (event: FormEvent<HTMLButtonElement>): void => {
    // 设置authRequest的状态为按钮的value值，这个值是'login'或'register'
    setAuthRequest(event.currentTarget.value as 'login' | 'register');
  };

  // 登录页面的JSX结构
  return (
    <div className={styles.container}> {/* 使用导入的样式定义容器 */}
      {/* <img
        alt='Atlas Device Sync' // 图片的替代文本
        className={styles.logo} // 应用于图片的样式
        src={logo} // 图片源
      /> */}
      <h1>
      Welcome To OneSticker
      </h1>
      <form onSubmit={handleSubmit} className={styles.form}> {/* 表单提交时调用handleSubmit */}
        <input
          className={styles.input} // 输入框的样式
          type='text' // 输入框类型
          placeholder='Email' // 占位符
          value={email} // 输入框的值
          onChange={(event) => setEmail(event.currentTarget.value)} // 输入框内容变化时的处理函数
          autoCorrect='off'     // 关闭自动更正，主要针对Safari浏览器
          autoCapitalize='none' // 关闭首字母自动大写，主要针对Safari浏览器
        />
        <input
          className={styles.input} // 密码框的样式
          type='password' // 密码框类型
          placeholder='Password (min. 6 chars)' // 占位符
          value={password} // 密码框的值
          onChange={(event) => setPassword(event.currentTarget.value)} // 密码框内容变化时的处理函数
        />
        {result.error && (
          <p className={styles.error}> {/* 错误信息的样式 */}
            {result.error.message} {/* 错误信息的内容 */}
          </p>
        )}
        <div className={styles.buttons}> {/* 按钮组的样式 */}
          <button
            className={styles.button} // 按钮的样式
            type='submit' // 按钮类型
            value='login' // 按钮的值
            onClick={handleButtonClicked} // 点击按钮时的处理函数
          >
            Log In 
          </button>
          {/* <button
            className={styles.button} // 按钮的样式
            type='submit' // 按钮类型
            value='register' // 按钮的值
            onClick={handleButtonClicked} // 点击按钮时的处理函数
          >
            Register 
          </button> */}
        </div>
      </form>
    </div>
  );
}
